import * as React from "react"

function DefaultIcon(props) {
  return (
    <svg
      width="1em"
      height="1em"
      viewBox="0 0 248 248"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      {...props}
    >
      <circle cx={124} cy={124} r={124} fill="#fff" />
      <circle cx={124} cy={124} r={124} fill="url(#prefix__pattern0)" />
      <defs>
        <pattern
          id="prefix__pattern0"
          patternContentUnits="objectBoundingBox"
          width={1}
          height={1}
        >
          <use
            xlinkHref="#prefix__image0"
            transform="matrix(.00343 0 0 .00388 -.085 -.556)"
          />
        </pattern>
        <image
          id="prefix__image0"
          width={567}
          height={800}
          xlinkHref="data:image/jpeg;base64,"
        />
      </defs>
    </svg>
  )
}

export default DefaultIcon;
